.YourAvail {
  text-align: center;
}

.YourAvail .heading {
  font-size: 40px;
  font-weight: 500;
  margin-top: 50px;
}

.YourAvail .sub-heading {
  font-size: 30px;
  font-weight: 400;
  margin: 2rem 0;
}

.YourAvail .calender-container {
  width: 600px;
  margin: 0 auto;
}

.YourAvail .calender-container .calender {
  width: 100%;
  border-radius: 10px;
}

.calender-container .react-calendar__navigation__label__labelText {
  font-size: 20px;
  font-weight: 500;
}

.CircleWrapper {
  text-align: center;
}

.AvailConfirm {
  background-color: #a8ea89;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  display: inline-block;
}

.AvailNone {
  background-color: #ee5858;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  display: inline-block;
}

.UpdateButton {
  padding: 11px 39px;
  font-size: 15px;
  font-weight: 500;
  border: 1px solid #18a0fb;
  background-color: #ffffff;
  color: #18a0fb;
  border-radius: 6px;
  margin-top: 30px;
  cursor: pointer;
}

@media (max-width: 1376px) {
  .YourAvail .heading {
    font-size: 34px;
    margin-top: 20px;
  }

  .YourAvail .sub-heading {
    font-size: 24px;
    margin: 1rem 0;
  }

  .YourAvail .calender-container {
    padding: 0 1rem;
  }
}
